.ProFile{
    width:100%;
}
.ProfileName{
    line-height: 18px;
}

.ProfileName .Name{
    text-transform: uppercase;
    font-size: 17px;
    font-weight: 700;
    color:var(--font-title);
}

.ProfileName span{
    font-weight:700;
    
}
.ProfileName .NickName{
    font-size: 13px;
    color:var(--font-title);
}

.ProfileImage{
    margin-top: 10px;
    
}

.ProfileImage img{
    width: 100%;
    border-radius: 3px;
}
.ProfileDetail{
    
    color:var(--font-title);;
    font-weight: 400;
}

.ProfileDetail .Title{
    color:var(--font-title);
    text-transform: uppercase;
    position: relative;
    display: inline-block;
    font-weight: 500 !important;
}

.ProfileDetail span{
    color:var(--font-title);
    font-weight: 600;
}

.ProfileDetail .Title span{
    position: absolute;
    height: 5px;
    width: 50px;
    bottom: 0.4;
    left: 0;
    background-color: var(--main-color);
    padding-top: 0;
    border-radius: 5px;
    
  }
  
.ProfileDetail .ProfileDescription{
    padding-top: 10px;
    padding-bottom: 10px;
  }
  
.ProfileDetail .Title .MuiTypography-root{
    font-size: 16px ;
    font-weight: 700 ;
}

/*Button*/
.ProFile .ButtonContainer{
    width:100%;
    display: flex ;
    justify-content: center ;
    margin-top: 10px;
}
.ProFile .ButtonContainer .CustomBtn{
    background-color: var(--main-color);
    font-size: 1rem;
    font-weight: 500;
    border:0;
    border-radius: 20px;
    outline: none;
    padding: .9375rem 2rem .875rem;
    line-height: 35px;
    text-transform: none;
}

.ProFile .ButtonContainer span {
    color:white;
    font-weight: 400;
}