/*about me*/

.title{
    position: relative;
    display: inline-block;
    padding: 0 3px;
    color: #44566c;
    
  }
.title .css-ag7rrr-MuiTypography-root{
    font-family: 'Archivo', sans-serif !important ;
    font-weight: 700 !important;
    font-size: 1.75rem;
}
.title span{
      position: absolute;
      height: 5px;
      width: 50px;
      bottom: 0.4;
      left: 3px;
      background-color: var(--main-color);
      padding-top: 0;
      border-radius: 5px;
  }
  
.title .aboutMe{
    padding-top: 15px;
    padding-left: 3px;
    font-size: 16px !important;
    font-weight: 400 !important;
}
/*End of about me*/



/*Timeline*/

.sectionTimeline .timelineContent{
    color:#44566c;
}
.sectionTimeline .css-2ulfj5-MuiTypography-root{
    font-family: 'Archivo', sans-serif !important ;
    font-weight: 700 !important;
    font-size: 1.5rem
}
.sectionTimeline .timelineTtile{
    font-size: 16px;
    font-weight: 500;
    color:#44566c;
}

.sectionTimeline .timelineDate{
    font-size: 14px;
    
}

.sectionTimeline .timelineDescription{
    font-size: small;
    margin-top: 6px;
}

/*End of Timeline*/

/*Skills*/
.title .css-ahj2mt-MuiTypography-root{
    font-family: 'Archivo', sans-serif !important ;
    font-weight: 700 ;
    font-size: 1.5rem;
}
.skill{
    border-radius: 8px;
    padding: 28px;
    box-shadow: 0px 0px 48px 0px rgba(4, 6, 4, 0.08);
    min-height:186px;
    height:100%;
    background-color: var(--main-color);
}
.skill .skillTitle{
    font-size:18px;
    font-weight: 500;
    line-height: 35px;
    color: var(--font-title);
}
.skill .skillDescription{
    display: flex;
    align-items: center;
    color: var(--font-title);
    font-weight: 400;
    line-height: 18px;
}

.skill .skillDescription .timelineDot{
    color: black;
    border-color: var(--main-color);
    padding: 2px;
    margin-right: 5px;
}
/*Footer*/
.Footer{
    font-family: 'Archivo', sans-serif;
    color:#8697a8;
    text-align: end;
    font-weight: 400;
    
}