@import url(https://fonts.googleapis.com/css2?family=Archivo&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root{
  --main-color:#304cfd;
  --font-title:#44566c;
}


body{
  background-image: url(https://bslthemes.site/vcard/wp-content/themes/vcard/assets/images/triangles-top.svg),url(https://bslthemes.site/vcard/wp-content/themes/vcard/assets/images/triangles-bottom.svg);
  background-position: left top,right bottom;
  background-repeat: no-repeat;
  background-size: inherit  !important;
  background-color: #f0f0f6 !important;          
  background-size: 100% 100%;           
  
}

.css-ahj2mt-MuiTypography-root {
  font-family: 'Archivo', sans-serif !important ;
}
.Spacing60{
  margin-top: 60px;
  margin-bottom: 60px;
}

.padbuttom{
  padding-bottom: 20px;
}

.padTop{
  padding-top: 20px;
}

@media (max-width:991px){
  .Spacing60{
    margin-top: 30px;
  }
}
.shadowContainer{
  box-shadow:  0px 2px 92px 0px rgba(0, 0, 0, 0.13);
  -webkit-box-shadow:0px 2px 92px 0px rgba(0, 0, 0, 0.13);
  -moz-box-shadow:0px 2px 92px 0px rgba(0, 0, 0, 0.13);
  
}
.ProfileContent{
  background: white;
  min-height: 100%;
  border-radius: 6px;
  -moz-border-radius: 6px;
  -webkit-border-radius: 6px;
  margin-bottom: 30px;
  padding: 20px;
}

.main-content{
  background: white;
  
  border-radius: 6px;
  -moz-border-radius: 6px;
  -webkit-border-radius: 6px;
  margin-bottom: 30px;
  padding: 30px;
}



.ProFile{
    width:100%;
}
.ProfileName{
    line-height: 18px;
}

.ProfileName .Name{
    text-transform: uppercase;
    font-size: 17px;
    font-weight: 700;
    color:var(--font-title);
}

.ProfileName span{
    font-weight:700;
    
}
.ProfileName .NickName{
    font-size: 13px;
    color:var(--font-title);
}

.ProfileImage{
    margin-top: 10px;
    
}

.ProfileImage img{
    width: 100%;
    border-radius: 3px;
}
.ProfileDetail{
    
    color:var(--font-title);;
    font-weight: 400;
}

.ProfileDetail .Title{
    color:var(--font-title);
    text-transform: uppercase;
    position: relative;
    display: inline-block;
    font-weight: 500 !important;
}

.ProfileDetail span{
    color:var(--font-title);
    font-weight: 600;
}

.ProfileDetail .Title span{
    position: absolute;
    height: 5px;
    width: 50px;
    bottom: 0.4;
    left: 0;
    background-color: var(--main-color);
    padding-top: 0;
    border-radius: 5px;
    
  }
  
.ProfileDetail .ProfileDescription{
    padding-top: 10px;
    padding-bottom: 10px;
  }
  
.ProfileDetail .Title .MuiTypography-root{
    font-size: 16px ;
    font-weight: 700 ;
}

/*Button*/
.ProFile .ButtonContainer{
    width:100%;
    display: flex ;
    justify-content: center ;
    margin-top: 10px;
}
.ProFile .ButtonContainer .CustomBtn{
    background-color: var(--main-color);
    font-size: 1rem;
    font-weight: 500;
    border:0;
    border-radius: 20px;
    outline: none;
    padding: .9375rem 2rem .875rem;
    line-height: 35px;
    text-transform: none;
}

.ProFile .ButtonContainer span {
    color:white;
    font-weight: 400;
}
.timeline{
    padding: 0;
}
/*Header Title*/
.timeline .timelineTitle{
    padding-top:7px;
    color:#44566c;
}

.timeline .timelineHead{
    min-height: 80px !important;
}

.timeline .MuiTimelineItem-root{
    min-height: 0px;
}

.timeline .MuiTimelineItem-missingOppositeContent:before{
    display: none;
}

.timeline .timelineDotHeader{
    color :black !important;
    background-color: var(--main-color) ;
    font-size: small !important;
    padding: 10px !important;
}
.timeline .timelineDotHeader > .MuiSvgIcon-root{
    font-size:20px;
    color: white;
}


/*Seperator Timelime*/
.timeline .timelineDot{
    color :black ;
    background-color: var(--main-color) ;
    font-size: small ;
}

.timeline .MuiTimelineConnector-root{
    background-color:#dedede ;
}

.timeline .SeperatorCustom{
    padding-left: 17px;
}


/*about me*/

.title{
    position: relative;
    display: inline-block;
    padding: 0 3px;
    color: #44566c;
    
  }
.title .css-ag7rrr-MuiTypography-root{
    font-family: 'Archivo', sans-serif !important ;
    font-weight: 700 !important;
    font-size: 1.75rem;
}
.title span{
      position: absolute;
      height: 5px;
      width: 50px;
      bottom: 0.4;
      left: 3px;
      background-color: var(--main-color);
      padding-top: 0;
      border-radius: 5px;
  }
  
.title .aboutMe{
    padding-top: 15px;
    padding-left: 3px;
    font-size: 16px !important;
    font-weight: 400 !important;
}
/*End of about me*/



/*Timeline*/

.sectionTimeline .timelineContent{
    color:#44566c;
}
.sectionTimeline .css-2ulfj5-MuiTypography-root{
    font-family: 'Archivo', sans-serif !important ;
    font-weight: 700 !important;
    font-size: 1.5rem
}
.sectionTimeline .timelineTtile{
    font-size: 16px;
    font-weight: 500;
    color:#44566c;
}

.sectionTimeline .timelineDate{
    font-size: 14px;
    
}

.sectionTimeline .timelineDescription{
    font-size: small;
    margin-top: 6px;
}

/*End of Timeline*/

/*Skills*/
.title .css-ahj2mt-MuiTypography-root{
    font-family: 'Archivo', sans-serif !important ;
    font-weight: 700 ;
    font-size: 1.5rem;
}
.skill{
    border-radius: 8px;
    padding: 28px;
    box-shadow: 0px 0px 48px 0px rgba(4, 6, 4, 0.08);
    min-height:186px;
    height:100%;
    background-color: var(--main-color);
}
.skill .skillTitle{
    font-size:18px;
    font-weight: 500;
    line-height: 35px;
    color: var(--font-title);
}
.skill .skillDescription{
    display: flex;
    align-items: center;
    color: var(--font-title);
    font-weight: 400;
    line-height: 18px;
}

.skill .skillDescription .timelineDot{
    color: black;
    border-color: var(--main-color);
    padding: 2px;
    margin-right: 5px;
}
/*Footer*/
.Footer{
    font-family: 'Archivo', sans-serif;
    color:#8697a8;
    text-align: end;
    font-weight: 400;
    
}
.header{
    margin-bottom: 30px;
    padding: 0 22px 0 0;
    border: 6px;
}
.header{
    
}
.footer{
    background: var(--main-color);
    color:white;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-between;
    min-height: 70px;
    border-radius: 8px;
    padding: 20px 25px;
    
}

.footer .footerCredit{
    font-size: small;
    text-align: end;
    color: #9c9c9c;
    
}


